import React from 'react'
import Link from 'gatsby-link'
import {withPrefix} from 'gatsby-link'
import introbgvid from '../../static/introbgvid.mp4'
import Typist from 'react-typist';

function Clip(props) {
  return (
    <video className='pageVidBG' reload="auto" loop="loop" src={props.url} autoPlay>
    </video>
  )
}



const IndexPage = () => (
  <div>
    <div className="videowrapper">
      <Clip url="./introbgvid.mp4"></Clip>
    </div>
    <div id="homeIntro">  
      <article>
        <div id="introText">
        </div>
        
        <Typist avgTypingDelay={30} cursor={{ hideWhenDone: true }} >
          <Typist.Delay ms={200}/>
          <h1>Hi people</h1>
          <Typist.Delay ms={500}/><p>Nice to meet you.</p>
          <Typist.Delay ms={1000}/><p>I'm currently working hard on another project.</p></Typist>
      </article>
    </div>
  </div>
)

export default IndexPage



